<template>
  <div class="popup__wrapper" ref="popup__wrapper">
    <div class="v-popup">
      <div class="v-popup__header">
        <i class="fas fa-times" @click="closePopup"></i>
        <div class="title">–ù–∞–ª–æ–≥–æ–≤—ã–π –≤—ã—á–µ—Ç</div>
      </div>

      <div class="v-popup__content">
        <div class="info">
          –ò—Å–ø–æ–ª—å–∑—É–π—Ç–µ –Ω–∞–ª–æ–≥–æ–≤—ã–π –≤—ã—á–µ—Ç —á—Ç–æ–±—ã –ø–æ–≥–∞—Å–∏—Ç—å –∏–ø–æ—Ç–µ–∫—É –¥–æ—Å—Ä–æ—á–Ω–æ. –†–∞–∑–º–µ—Ä
          –Ω–∞–ª–æ–≥–æ–≤–æ–≥–æ –≤—ã—á–µ—Ç–∞ —Å–æ—Å—Ç–∞–≤–ª—è–µ—Ç –Ω–µ –±–æ–ª–µ–µ 13% –æ—Ç —Å–≤–æ–µ–≥–æ –æ—Ñ–∏—Ü–∏–∞–ª—å–Ω–æ–≥–æ
          –≥–æ–¥–æ–≤–æ–≥–æ –¥–æ—Ö–æ–¥–∞.
        </div>

        <div class="salary">
          <div class="salary__title">–í–∞—à–∞ –∑–∞—Ä–ø–ª–∞—Ç–∞ –≤ –º–µ—Å—è—Ü</div>
          <input
            :class="[
              $v.salary.$error || salary == '0 ‚ÇΩ' ? 'salary__error' : '',
            ]"
            type="text"
            placeholder="–í–≤–µ–¥–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ"
            v-model="salary"
            v-money="salary !== null ? money : null"
          />
          <small
            v-if="
              ($v.salary.$dirty && !$v.salary.required) ||
              ($v.salary.$dirty && salary == '0 ‚ÇΩ')
            "
            >–ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è</small
          >
          <button class="salary__calculate" @click="—Åalculate(salary)">
            –†–∞—Å—Å—á–∏—Ç–∞—Ç—å
          </button>
        </div>

        <div class="payment" v-if="paymentVisible">
          <p>–ò—Ç–æ–≥–æ –º–æ–∂–µ—Ç–µ –≤–Ω–µ—Å—Ç–∏ –≤ –∫–∞—á–µ—Å—Ç–≤–µ –¥–æ—Å—Ä–æ—á–Ω—ã—Ö:</p>
          <div
            class="payment__year"
            v-for="elem in paymentList"
            :key="elem.index"
          >
            <i class="fas fa-check" v-if="elem.checkbox == true"></i>
            <i class="fas fa-check disabled-checkbox" v-else></i>
            {{ elem.tax }} <span>{{ elem.year }} –≥–æ–¥</span>
          </div>
        </div>

        <div class="tags">
          <p>–ß—Ç–æ —É–º–µ–Ω—å—à–∞–µ–º?</p>
          <button :class="{ active: !strict }" @click="strict = false">
            –ü–ª–∞—Ç–µ–∂
          </button>
          <button :class="{ active: strict }" @click="strict = true">
            –°—Ä–æ–∫
          </button>
        </div>
      </div>

      <div class="v-popup__footer">
        <button class="add-btn">–î–æ–±–∞–≤–∏—Ç—å</button>
      </div>
    </div>
  </div>
</template>

<script>
import { required } from "../../node_modules/vuelidate/lib/validators";
export default {
  name: "v-popup",
  props: {
    visiblePopup: {
      type: Boolean,
    },
  },
  data() {
    return {
      strict: false,
      paymentVisible: false,
      salary: null,
      paymentList: [],
      maxTax: 260000,
      money: {
        thousands: " ",
        suffix: " ‚ÇΩ",
        precision: 0,
      },
    };
  },
  validations: {
    salary: { required },
  },
  methods: {
    //–û—Å—É—â–µ—Å—Ç–≤–ª—è–µ–º –≤—ã–±–æ—Ä –ø—Ä–∞–≤–∏–ª—å–Ω–æ–≥–æ –ø—Ä–µ–¥–ª–æ–≥–∞ –≤ —Å–ø–∏—Å–∫–µ –¥–æ—Å—Ä–æ—á–Ω—ã—Ö –ø–ª–∞—Ç–µ–∂–µ–π
    declOfNumStart(number, titles) {
      if (number == 2) {
        return titles[1];
      }
      return titles[0];
    },

    //–û—Å—É—â–µ—Å—Ç–≤–ª—è–µ–º –≤—ã–±–æ—Ä –ø—Ä–∞–≤–∏–ª—å–Ω–æ–≥–æ –æ–∫–æ–Ω—á–∞–Ω–∏—è –≤ —Å–ø–∏—Å–∫–µ –¥–æ—Å—Ä–æ—á–Ω—ã—Ö –ø–ª–∞—Ç–µ–∂–µ–π
    declOfNumEnd(number, titles) {
      let arr1 = [1, 4, 5, 9, 0];
      let arr2 = [2, 6, 7, 8];
      number = Math.abs(number) % 100;
      var n1 = number % 10;
      if ((number > 10 && number < 20) || arr1.includes(n1)) {
        return titles[0];
      }
      if (arr2.includes(n1)) {
        return titles[1];
      }
      if (n1 == 3) {
        return titles[2];
      }
    },

    //–†–∞—Å—Å—á–∏—Ç—ã–≤–∞–µ–º –∫–æ–ª-–≤–æ –≤–æ–∑–º–æ–∂–Ω—ã—Ö –ª–µ—Ç –¥–ª—è –¥–æ—Å—Ä–æ—á–Ω—ã—Ö –≤—ã–ø–ª–∞—Ç
    —Åalculate(salary) {
      this.paymentList = [];
      //–ï—Å–ª–∏ –∏–Ω–ø—É—Ç –ø—É—Å—Ç–æ–π –∏–ª–∏ = 0 —Ç–æ–≥–¥–∞ –≤—ã–≤–æ–¥–∏–º –æ—à–∏–±–∫—É –≤–∞–ª–∏–¥–∞—Ü–∏–∏ –Ω–∞ –∏–Ω–ø—É—Ç–µ
      if (salary == null || salary == "0 ‚ÇΩ") {
        this.paymentVisible = false;
        this.$v.$touch();
        return;
      }
      //–ï—Å–ª–∏ –∏–Ω–ø—É—Ç —Å–æ–¥–µ—Ä–∂–∏—Ç –∫–∞–∫–æ–µ-–ª–∏–±–æ –∑–Ω–∞—á–µ–Ω–∏–µ, —Ç–æ —É–¥–∞–ª—è–µ–º –ª–∏—à–Ω–∏–µ —Å–∏–º–≤–æ–ª—ã, –∏ —Ä–∞—Å—á–∏—Ç—ã–≤–∞–µ–º –≤–æ–∑–º–æ–∂–Ω—ã–µ –≤—ã–ø–ª–∞—Ç—ã –ø–æ –≥–æ–¥–∞–º
      salary = salary.replace(/\s/g, "");
      salary = salary.replace("‚ÇΩ", "");
      let maxTax = this.maxTax;
      let count = 0;
      let tax = salary * 12 * 0.13;
      while (maxTax > tax) {
        count += 1;
        maxTax = maxTax - tax;
        const obj = {
          year:
            this.declOfNumStart(count, ["–≤ ", "–≤–æ "]) +
            String(count) +
            this.declOfNumEnd(count, ["-—ã–π", "-–æ–π", "-–∏–π"]),
          tax: tax,
          checkbox: true,
        };
        this.paymentList.push(obj);
      }
      count += 1;
      this.paymentList.push({
        year:
          this.declOfNumStart(count, ["–≤ ", "–≤–æ "]) +
          String(count) +
          this.declOfNumEnd(count, ["-—ã–π", "-–æ–π", "-–∏–π"]),
        tax: maxTax,
        checkbox: false,
      });
      this.paymentVisible = true;
    },

    //–ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
    closePopup() {
      this.$emit("closePopup");
    },
  },

  //–ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ, –µ—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∫–ª–∏–∫–Ω—É–ª –∑–∞ –µ–≥–æ –ø—Ä–µ–¥–µ–ª—ã
  mounted() {
    document.addEventListener("click", (item) => {
      if (item.target === this.$refs["popup__wrapper"]) {
        this.closePopup();
      }
    });
  },
};
</script>

<style lang="scss">
@import url("../scss/app.scss");
.fa-times {
  color: red;
  font-size: 27px;
  position: absolute;
  right: 27px;
  top: 27px;
  cursor: pointer;
}
.fa-check {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
      255.35deg,
      #dc3131 0.83%,
      rgba(255, 79, 79, 0) 108.93%
    ),
    #ff5e56;
  color: #fff;
  border-radius: 6px;
  width: 20px;
  height: 20px;
  margin-right: 11px;
}
.disabled-checkbox {
  background: #ffffff;
  border: 1px solid #dfe3e6;
  border-radius: 6px;
}
.square {
  width: 20px;
  height: 20px;
  background: #ffffff;
  border: 1px solid #dfe3e6;
  box-sizing: border-box;
  border-radius: 6px;
}
.popup__wrapper {
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  min-height: 100vh;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
}
.v-popup {
  padding: 32px;
  position: fixed;
  top: 50px;
  width: 552px;
  background: #fff;
  border-radius: 30px;
  max-height: 90vh;
  overflow: auto;
  &::-webkit-scrollbar {
    width: 0px;
  }
  &__header {
    .title {
      font-family: Lab Grotesque;
      font-style: normal;
      font-weight: 500;
      font-size: 28px;
      line-height: 40px;
      margin-bottom: 16px;
    }
  }
  &__content {
    .info {
      max-width: 95%;
      font-family: Lab Grotesque;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 24px;
      color: #808080;
      margin-bottom: 30px;
    }
    .salary {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 16px;
      &__title {
        font-family: Lab Grotesque;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        color: #000000;
        margin-bottom: 8px;
      }
      &__calculate {
        padding: 0;
        font-family: Lab Grotesque;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        color: #ea0029;
      }
      &__error {
        border: 1px solid #ea0029 !important;
      }
      small {
        font-family: Lab Grotesque;
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 12px;
        color: #ea0029;
        margin-bottom: 4px;
      }
      input {
        padding: 12px 10px;
        width: 100%;
        outline: none;
        border: 1px solid #dfe3e6;
        margin-bottom: 4px;
        border-radius: 3px;
        &:hover {
          border: 1px solid #000000;
        }
        &:disabled {
          border: 1px solid #808080;
        }
      }
      input::-webkit-outer-spin-button,
      input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
      input[type="number"] {
        -moz-appearance: textfield;
      }
    }

    .payment {
      position: relative;
      p {
        margin-bottom: 16px;
        font-family: Lab Grotesque;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        color: #000000;
      }
      span {
        font-family: Lab Grotesque;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        color: #808080;
      }
      input {
        margin-right: 11px;
      }
      &__year {
        font-family: Lab Grotesque;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 24px;
        color: #000000;
        border-bottom: 1px solid #dfe3e6;
        padding-bottom: 16px;
        margin-bottom: 16px;
      }
    }

    .tags {
      margin-bottom: 40px;
      display: flex;
      align-items: center;
      p {
        font-family: Lab Grotesque;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        color: #000000;
        margin-right: 76px;
      }
      button {
        padding: 6px 12px;
        background: #eef0f2;
        border-radius: 50px;
        color: #000;
        margin-right: 16px;
        font-family: Lab Grotesque;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 24px;
        &.active {
          background: linear-gradient(
              255.35deg,
              #dc3131 0.83%,
              rgba(255, 79, 79, 0) 108.93%
            ),
            #ff5e56;
          border-radius: 50px;
          color: #fff;
        }
      }
    }
  }
  &__footer {
    position: relative;
    .add-btn {
      background: linear-gradient(
          255.35deg,
          #dc3131 0.83%,
          rgba(255, 79, 79, 0) 108.93%
        ),
        #ff5e56;
      box-shadow: 0px 0px 24px rgba(234, 0, 41, 0.33);
      border-radius: 6px;
      width: 100%;
      font-family: Lab Grotesque;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      &:disabled,
      [disabled] {
        border: 1px solid #bec5cc;
        color: #fff;
        cursor: default;
        box-shadow: none;
        background: #bec5cc;
      }
    }
  }
}
</style>